@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

html{
    @apply scroll-smooth;
}

body{
    @apply bg-[#111111];
}

h1{
    @apply text-4xl md:text-5xl;
}

h2{
    @apply text-3xl md:text-4xl;
}

h3{
    @apply text-2xl md:text-3xl;
}

.wave-white {
    background-image: url(./assets/wave.svg);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position-y: 100%;
}

.footer-btn{
    @apply text-2xl md:text-4xl p-3 hover:scale-110 hover:bg-gray-100/50 border-white border-2 hover:border-gray-300 rounded-3xl duration-300;
}

.card {
    @apply bg-gray-100 border-2 border-gray-300 rounded-lg;
}

.prj-card {
    @apply flex flex-col justify-between m-2 p-4 max-w-md shadow-xl mx-auto md:mx-4 duration-150;
}

a.link {
    @apply text-nevBlue underline font-bold;
}

.important {
    @apply text-nevBlue
}

.nav-link {
    @apply hover:text-nevBlue duration-200
}